import uniSwipeAction from '@/components/uni-swipe-action/uni-swipe-action.vue'
import uniSwipeActionItem from '@/components/uni-swipe-action-item/uni-swipe-action-item.vue'
export default {
	//import引入的组件需要注入到对象中才能使用
	components: {
		uniSwipeAction,
		uniSwipeActionItem
	},
	data() {
		//这里存放数据
		return {
			openId:'',
			carData: [],
			options: [{
				text: '删除',
				style: {
					backgroundColor: '#B4282D'
				}
			}],
			allFlag:false,
			count:0
		};
	},
	//监听属性 类似于data概念
	computed: {},
	
	//监控data中的数据变化
	watch: {},
	//方法集合
	methods: {
		onClick(index,item) {
			
			let goodid=item.id
			console.log(goodid)
			uni.request({
				url: this.$api(`cart/deleteAction?id=${goodid}`),
				method: 'GET',
				data: {},
				success: res => {
					console.log(res)
					uni.request({
						url: this.$api(`cart/cartList?openId=${this.openId}`),
						method: 'GET',
						data: {
							
						},
						success: res => {
							// console.log(res)
							this.carData = res.data.data
							console.log(this.carData)
						},
						fail: () => {},
						complete: () => {}
					});
				},
				fail: () => {},
				complete: () => {}
			});
		},
		changeChecked(index) {
			console.log(index)
		},
		change(open) {
			console.log('当前开启状态：' + open)
		},
		checkall(){
			if(this.allFlag){
				this.allFlag=false
				let arr= this.carData
				arr.map(item=>{
					item.checked=false
				})
			}else{
				this.allFlag=true
				let arr= this.carData
				arr.map(item=>{
					item.checked=true
				})
			}
		},
		submit(){
			
		}
	},
	//生命周期 - 创建完成（可以访问当前this实例）
	created() {

	},
	onShow() {
		console.log('onshow')
		this.openId=uni.getStorageSync('openId')
		// console.log(opid)
		uni.request({
			url: this.$api(`cart/cartList?openId=${this.openId}`),
			method: 'GET',
			data: {},
			success: res => {
				// console.log(res)
				this.carData = res.data.data
				console.log(this.carData)
			},
			fail: () => {},
			complete: () => {}
		});
	},
	//生命周期 - 挂载完成（可以访问DOM元素）
	mounted() {
		// let opid = uni.getStorageSync('openId')
		this.openId=uni.getStorageSync('openId')
		// console.log(opid)
		uni.request({
			url: this.$api(`cart/cartList?openId=${this.openId}`),
			method: 'GET',
			data: {},
			success: res => {
				// console.log(res)
				this.carData = res.data.data
				console.log(this.carData)
			},
			fail: () => {},
			complete: () => {}
		});
	},
	beforeCreate() {}, //生命周期 - 创建之前
	beforeMount() {}, //生命周期 - 挂载之前
	beforeUpdate() {}, //生命周期 - 更新之前
	updated() {}, //生命周期 - 更新之后
	beforeDestroy() {}, //生命周期 - 销毁之前
	destroyed() {}, //生命周期 - 销毁完成
	activated() {}, //如果页面有keep-alive缓存功能，这个函数会触发
}
